import React from "react";
import Layout from "./Layout";
import { SEO } from "theme-seo";
import { GetCities, provinces } from "./Iran";
import Img from "gatsby-image";

const Agent = ({ data, pageContext }) => {
  let parseServerUrl = pageContext.parseServerUrl;

  const [registered, setRegistered] = React.useState(false);
  const [agent, setAgent] = React.useState({
    objectId: "",
    Name: "",
    FamilyName: "",
    Ostan: "",
    Shahr: "",
    DarkhstNamayandegi: false,
  });

  const [states, setStates] = React.useState([]);
  const [cities, setCities] = React.useState([]);
  const defaultButtonState = {
    disabled: true,
    buttonStyle: "cursor-not-allowed",
  };

  const [buttonState, setButtonState] = React.useState(defaultButtonState);
  const claims = getCookie("Claims");
  const user = claims ? JSON.parse(decodeURIComponent(claims)) : null;

  const sessionToken = claims ? JSON.parse(decodeURIComponent(claims)).SessionToken : null;

  React.useEffect(function () {
    if (sessionToken) {
      fetch(parseServerUrl + "parse/Users/me", {
        headers: {
          "X-Parse-Session-Token": `${sessionToken}`,
          "X-Parse-Application-Id": `${pageContext.applicationId}`,
        },
      })
        .then(function (results) {
          return results.json();
        })
        .then(function (r) {
          let newUser = {};
          newUser.objectId = r.objectId;
          newUser.Name = r.Name || "";
          newUser.FamilyName = r.FamilyName || "";
          newUser.username = r.username || "";
          newUser.Ostan = r.Ostan || "";
          newUser.Shahr = r.Shahr || "";
          setAgent(newUser);
          CallSetButtonState(newUser);
          SetAllStates(newUser);
        });
    }
  }, []);

  function SetAllStates(user) {
    provinces.unshift({ id: "*", name: "" });
    setStates(provinces);
    if (user.Ostan) {
      let stateId = provinces.filter((s) => s.name === user.Ostan)[0].id;
      SetRelatedCities(stateId, user);
    }
  }

  function getCookie(name) {
    if (typeof document === "undefined") return;
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(";").shift();
  }

  function Login() {
    window.location =
      pageContext.authUrl + "?returnUrl=" + window.location + encodeURIComponent("#register");
    return;
  }

  function AddREquest() {
    const clonedAgent = { ...agent };
    clonedAgent.DarkhstNamayandegi = true;
    setAgent(clonedAgent);

    fetch(parseServerUrl + "/parse/Users/" + agent.objectId, {
      method: "PUT",
      headers: {
        "X-Parse-Application-Id": `${pageContext.applicationId}`,
        "X-Parse-Session-Token": `${sessionToken}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify(clonedAgent),
    }).then(function (response) {
      setRegistered(true);
      return response.json();
    });
  }

  function SetRelatedCities(id, user) {
    let stateId = id;

    let stateName = document.getElementById("states").value;
    if (!user) {
      const clone = { ...agent };
      clone["Ostan"] = stateName;
      clone["Shahr"] = "";
      setAgent(clone);
    }

    if (!id) {
      let stateName = document.getElementById("states").value;

      if (stateName) stateId = states.filter((s) => s.name === stateName)[0].id;
    }
    if (stateId) {
      let cities = GetCities(stateId);
      cities.unshift({ id: "*", name: "" });
      setCities(cities);
    } else {
      setCities([]);
    }
  }

  function HandleInputChange(event) {
    let element = event.target;
    if (element.name === "phone") {
      if (element.value !== "" && !/^[0-9\b]+$/.test(element.value)) {
        return;
      }
    }

    let agentClone = { ...agent };
    agentClone[event.target.name] = element.value;
    setAgent(agentClone);

    CallSetButtonState(agentClone);
  }

  function CallSetButtonState(agent) {
    let buttonStateClone = { ...buttonState };
    buttonStateClone.disabled = !(agent.FamilyName && agent.Name && agent.Ostan && agent.Shahr);
    buttonStateClone.buttonStyle = buttonStateClone.disabled
      ? "cursor-not-allowed"
      : "opacity-100 hover:bg-green-600";
    setButtonState(buttonStateClone);
  }

  return (
    <Layout faqs={pageContext.faqs}>
      <SEO
        title={pageContext.title}
        imageUrl={data.banner?.publicURL}
        description={pageContext.description}
        faqs={pageContext.faqs}
      />
      {data.banner && data.banner.extension === "svg" ? (
          <img
            style={{ width: "100%" }}
            width="1519"
            height="624"
            alt={data.banner.name}
            src={data.banner.publicURL}
          />
        ) : (
          <Img alt={data.banner.name} fluid={data.banner.childImageSharp.fluid} />)}
      <div className="w-5/6 lg:w-4/5  mx-auto">
        <div className="WordpressStyles  my-16 mx-auto">
          <div className="mx-4 text-gray-700">
            <div
              className="  "
              dangerouslySetInnerHTML={{ __html: data.markdownRemark.html }}
            ></div>
          </div>
        </div>
        <h2 className="text-center text-lg lg:text-2xl mb-16 text-green-700">
          فرایند اعطای نمایندگی فروش یا عاملیت
        </h2>
        <img
          width="1215"
          height="220"
          alt={data.registerProcess.name}
          src={data.registerProcess.publicURL}
          className="hidden lg:block"
        />

        <img alt={data.vertical.name} className="lg:hidden mx-auto" src={data.vertical.publicURL} />
        <div>
          <h2 className="text-center mt-16 text-green-700">مزایای اخذ نمایندگی</h2>
          <img
            alt={data.whyRayma.name}
            width="900"
            height="600"
            className="hidden lg:block mb-16 mx-auto"
            src={data.whyRayma.publicURL}
          />
          <img
            alt={data.whyRaymaVertical.name}
            width="310"
            height="597"
            className="lg:hidden mb-16 mx-auto"
            src={data.whyRaymaVertical.publicURL}
          />
        </div>
        <div className=" flex flex-col my-10">
          <div
            id="register"
            style={{ justifyContent: "space-between", backgroundColor: "whitesmoke" }}
            className="flex flex-col-reverse lg:flex-row mx-auto lg:w-full "
          >
            {user && (
              <div style={{ flexGrow: 1 }} className="mx-8 lg:mx-16">
                <h2 className=" text-center mb-8 text-green-700">فرم درخواست نمایندگی رایما</h2>
                <form>
                  <div>
                    <label className="block text-gray-700 text-sm font-bold my-2 mt-3">
                      نام
                      <span className="text-red-800 m-1">*</span>
                    </label>
                    <input
                      className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                      type="text"
                      name={"Name"}
                      value={agent.Name}
                      onChange={HandleInputChange}
                    />
                  </div>
                  <div>
                    <label className="block text-gray-700 text-sm font-bold my-2  mt-4">
                      نام خانوادگی
                      <span className="text-red-800 m-1">*</span>
                    </label>
                    <input
                      className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                      type="text"
                      name="FamilyName"
                      value={agent.FamilyName}
                      onChange={HandleInputChange}
                    />
                  </div>

                  <div className="w-full ">
                    <label className={" block font-bold my-2 mt-4"}>
                      استان
                      <span className="text-red-800 m-1">*</span>
                    </label>
                    <select
                      id="states"
                      name="states"
                      value={agent.Ostan}
                      onChange={() => SetRelatedCities(null, null)}
                      className={"w-full border shadow rounded h-10 focus:shadow-outline"}
                    >
                      {states.map((s) => (
                        <option className="hover:bg-teal-500" key={s.id} id={s.id} value={s.name}>
                          {s.name}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div className="w-full">
                    <label className={" block font-bold my-2 mt-4"}>
                      شهر
                      <span className="text-red-800 m-1">*</span>
                    </label>

                    <select
                      name="Shahr"
                      id="cities"
                      value={agent.Shahr}
                      onChange={HandleInputChange}
                      className={"w-full border shadow rounded h-10 focus:shadow-outline"}
                    >
                      {cities.length > 0 &&
                        cities.map((city) => (
                          <option
                            className="hover:bg-teal-500"
                            key={city.id}
                            id={city.id}
                            value={city.name}
                          >
                            {city.name}
                          </option>
                        ))}
                    </select>
                  </div>
                  <button
                    type={"button"}
                    onClick={AddREquest}
                    disabled={buttonState.disabled}
                    className={
                      "m-2 bg-teal-600 text-white font-bold opacity-50  py-2 px-4 rounded-lg w-32 my-8 " +
                      buttonState.buttonStyle
                    }
                  >
                    ارسال
                  </button>
                  {registered && (
                    <div className="text-green-600 font-bold mx-auto">
                      <label>
                        درخواست شما با موفقیت ثبت شد، در اصرع وقت با شما تماس گرفته خواهد شد.
                      </label>
                    </div>
                  )}
                </form>
              </div>
            )}
            {!user && (
              <div className="text-center" style={{ alignSelf: "center", flexGrow: 1 }}>
                <div className="flex flex-col">
                  <img
                    width="250"
                    alt={data.rayma.name}
                    style={{ alignSelf: "center" }}
                    className="m-6"
                    src={data.rayma.childImageSharp.fixed.src}
                  />
                  <p className="font-bold text-gray-600 text-center">
                    شما هم میتوانید با افتخار عضو خانواده بزرگ رایورز باشید.
                  </p>
                  <button
                    type={"button"}
                    onClick={Login}
                    className={
                      "bg-orange-600 text-white font-bold p-4 px-6 rounded-full  my-8 mx-auto "
                    }
                  >
                    ورود جهت اخذ نمایندگی
                  </button>
                </div>
              </div>
            )}
            <img
              alt={data.file.name}
              style={{ alignSelf: "center" }}
              className="lg:w-1/2"
              src={data.file.childImageSharp.fixed.src}
            />
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Agent;

export const query = graphql`
  query($pageSlug: String) {
    markdownRemark(fields: { slug: { regex: $pageSlug } }) {
      html
      fields {
        slug
      }
      frontmatter {
        title
        order
      }
    }

    banner: file(name: { eq: "agent" }, extension: { eq: "svg" }) {
      name
      publicURL
      extension
      childImageSharp {
        fluid(maxWidth: 2500) {
          ...GatsbyImageSharpFluid
        }
      }
    }

    file(name: { eq: "map" }) {
      name
      publicURL
      extension
      childImageSharp {
        fixed(width: 2500) {
          src
        }
      }
    }

    whyRayma: file(name: { eq: "whyAgent" }) {
      name
      publicURL
      extension
      childImageSharp {
        fixed(width: 1000) {
          src
        }
      }
    }

    whyRaymaVertical: file(name: { eq: "whyAgent-vertical" }) {
      name
      publicURL
      extension
      childImageSharp {
        fixed(width: 1000) {
          src
        }
      }
    }

    registerProcess: file(name: { eq: "registerProcess" }) {
      name
      publicURL
      extension
      childImageSharp {
        fixed {
          src
        }
      }
    }

    vertical: file(name: { eq: "processVertical" }) {
      name
      publicURL
      extension
      childImageSharp {
        fixed {
          src
        }
      }
    }

    rayma: file(name: { eq: "rayma" }) {
      name
      publicURL
      extension
      childImageSharp {
        fixed {
          src
        }
      }
    }
  }
`;
